import React from 'react';

function ActionInfoOverlay({ actionInfo, volitionInfoAsStrings, onClose }) {
  if (!actionInfo) return null;

  return (
    <div className="action-info-overlay" onClick={onClose}>
      <div className="action-info" onClick={e => e.stopPropagation()}>
        <button 
          className="close-button"
          onClick={onClose}
        >
          ×
        </button>
        <h3>Action Details</h3>
        <p><strong>Intent:</strong> {actionInfo.intent}</p>
        <p><strong>Score:</strong> {actionInfo.score}</p>
        
        <h4>Social Considerations</h4>
        {volitionInfoAsStrings && volitionInfoAsStrings[actionInfo.intent]?.map((consideration, index) => (
          <div key={index} className="consideration-item">
            <p><strong>Description:</strong> {consideration.description}</p>
            <p><strong>Weight:</strong> {consideration.weight}</p>
            {consideration.predicates.length > 0 && (
              <>
                <p><strong>Predicates:</strong></p>
                <ul>
                  {consideration.predicates.map((predicate, pIndex) => (
                    <li key={pIndex}>{predicate}</li>
                  ))}
                </ul>
              </>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

export default ActionInfoOverlay; 