import React from 'react';
import CharacterSelector from './CharacterSelector';
import PracticeSelector from './PracticeSelector';

const SelectPracticeView = ({ 
  initiator, setInitiator, 
  responder, setResponder, 
  selectedPractice, setSelectedPractice,
  handlePracticeStart,
  rawCast,
  rawPractices 
}) => {
  return (
    <>
      <h1>Create an Interaction</h1>
      <div className="character-grid">
        <div className="selector-column">
          <h3>Initiator</h3>
          <CharacterSelector 
            characters={rawCast.characters}
            onSelectButtonClicked={setInitiator}
            disabledCharacter={responder}
          />
          {initiator && (
            <div className="selected-text">
              Selected: {initiator.name}
            </div>
          )}
        </div>

        <div className="selector-column">
          <h3>Responder</h3>
          <CharacterSelector 
            characters={rawCast.characters}
            onSelectButtonClicked={setResponder}
            disabledCharacter={initiator}
          />
          {responder && (
            <div className="selected-text">
              Selected: {responder.name}
            </div>
          )}
        </div>

        <div className="selector-column">
          <h3>Social Practice</h3>
          <PracticeSelector 
            practices={rawPractices.socialPractices}
            onSelectPractice={setSelectedPractice}
          />
          {selectedPractice && (
            <div className="selected-text">
              Selected: {selectedPractice}
            </div>
          )}
        </div>

        <div className="interaction-column selector-column">
          <h3>&nbsp;</h3>
          <button 
            onClick={handlePracticeStart}
            className="start-button"
            disabled={!initiator || !responder || !selectedPractice}
          >
            Start Interaction
          </button>
        </div>
      </div>
    </>
  );
};

export default SelectPracticeView; 