import React, { useState } from 'react';
import ActionInfoOverlay from './ActionInfoOverlay';

function PracticeView({ 
  initiator, 
  responder, 
  selectedPractice, 
  onActionSelected, 
  actions, 
  isLoading,
  cifInterface,
  performanceInfo,
  action,
  volitionInfoAsStrings,
  isTerminal,
  onChooseNextInteraction
}) {
  const [selectedActionInfo, setSelectedActionInfo] = useState(null);

  // Determine who should act next (opposite of last speaker)
  const getNextActor = () => {
    if (!cifInterface?.practiceDialogueHistory?.length) {
      return initiator.name; // First action
    }
    const lastSpeaker = cifInterface.practiceDialogueHistory[cifInterface.practiceDialogueHistory.length - 1].speaker;
    return lastSpeaker === initiator.name ? responder.name : initiator.name;
  };

  const handleInfoClick = (action) => {
    setSelectedActionInfo(action);
  };

  return (
    <div className="combined-view">
      {/* Title displaying the practice name, initiator, and responder */}
      <h1>{selectedPractice}: {initiator?.name} and {responder?.name}</h1>

      {/* Show dialogue history if it exists */}
      {cifInterface?.practiceDialogueHistory?.length > 0 && (
        <div className="performance-text">
          <div className="dialogue-panel">
            {cifInterface.practiceDialogueHistory.map((lineInfo, index, array) => (
              <p 
                key={index} 
                className={index === array.length - 1 ? 'latest-dialogue' : ''}
              >
                <strong>{lineInfo.speaker} {lineInfo.intent ? `(${lineInfo.intent})` : ''}:</strong> {lineInfo.line}
              </p>
            ))}
          </div>
          
          {performanceInfo?.effectPredicateTexts && performanceInfo.effectPredicateTexts.length > 0 && (
            <div className="state-changes">
              <h4>Social State Changes:</h4>
              <ul >
                {performanceInfo.effectPredicateTexts.map((effect, index) => (
                  <li key={index}>{effect}</li>
                ))}
              </ul>
            </div>
          )}
        </div>
      )}

      {/* Show action selection if actions are available and not terminal */}
      {!isTerminal && actions.length > 0 && (
        <div className="action-selection">
          <div className="interaction-summary">
            <p><strong>Choose what {getNextActor()} should do?</strong></p>
          </div>
          <div className="interaction-buttons">
            {actions.map((action, index) => (
              <div key={index} className="action-button-group">
                <button 
                  className="action-button"
                  onClick={() => onActionSelected(action.label)}
                  disabled={isLoading}
                >
                  <div className="action-label">{action.label}</div>
                  <div className="action-details">
                    <span className="action-intent">{action.intent}</span>
                    <span className="action-score">Score: {action.score}</span>
                  </div>
                </button>
                <button
                  className="info-button"
                  onClick={() => handleInfoClick(action)}
                  disabled={isLoading}
                >
                  i
                </button>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Show the button to choose the next interaction if terminal is true */}
      {isTerminal && (
        <button 
          className="start-button"
          onClick={onChooseNextInteraction}
        >
          Choose next interaction
        </button>
      )}

      <ActionInfoOverlay 
        actionInfo={selectedActionInfo}
        volitionInfoAsStrings={volitionInfoAsStrings}
        onClose={() => setSelectedActionInfo(null)}
      />
    </div>
  );
}

export default PracticeView; 