import React, { useState } from 'react';
import './Game.css';
import SelectPracticeView from './SelectPracticeView';
// import SelectActionView from './SelectActionView';
// import PerformanceView from './PerformanceView';
import PracticeView from './PracticeView';

import * as cifInterface from '../sim/cif-au/src/cif-interface.js';

cifInterface.initializeCiF();

function Game() {
  const [initiator, setInitiator] = useState(null);
  const [responder, setResponder] = useState(null);
  const [selectedPractice, setSelectedPractice] = useState('');
  const [isInteractionStarted, setIsInteractionStarted] = useState(false);
  const [currentView, setCurrentView] = useState('practice');
  const [actions, setActions] = useState([]);
  const [currentPerformanceInfo, setCurrentPerformanceInfo] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isContinueLoading, setIsContinueLoading] = useState(false);
  const [actionLabel, setActionLabel] = useState('');
  const [isTerminal, setIsTerminal] = useState(false);

  const handlePracticeStart = () => {
    if (!initiator || !responder || !selectedPractice) {
      alert('Please select both characters and a social practice before starting.');
      return;
    }
    cifInterface.launchPractice(selectedPractice, initiator.name, responder.name);
    cifInterface.getActions();
    setActions(cifInterface.mostRecentVolitionInfo.actions);
    setIsInteractionStarted(true);
    setCurrentView('action');
  };

  const handleActionSelected = async (actionLabel) => {
    setIsLoading(true);
    setActionLabel(actionLabel);
    try {
      let actionPerformedInfo = await cifInterface.performAction(actionLabel);
      setCurrentPerformanceInfo(actionPerformedInfo);
      console.log(actionPerformedInfo);

      setIsContinueLoading(true);
      try {
        let terminal = await cifInterface.isTerminal();
        setIsTerminal(terminal);
  
        if (!terminal) {
          cifInterface.getActions();
          setActions(cifInterface.mostRecentVolitionInfo.actions);
          let tmp = initiator;
          setInitiator(responder);
          setResponder(tmp);
        } 
      } finally {
        setIsContinueLoading(false);
      }
    } finally {
      setIsLoading(false);
    }
  };



  const handleChooseNextInteraction = () => {
    setIsTerminal(false);
    setIsInteractionStarted(false);
    setInitiator(null);
    setResponder(null);
    setSelectedPractice('');
    setActions([]);
    setCurrentPerformanceInfo(null);
  };

  const handlePracticeSelected = (practice) => {
    // cifInterface.getActions();
    // console.log(cifInterface.mostRecentVolitionInfo.actions);
    // setActions(cifInterface.mostRecentVolitionInfo.actions);
  };

  const getCurrentView = () => {
    if (!isInteractionStarted) {
      return (
        <SelectPracticeView
          initiator={initiator}
          setInitiator={setInitiator}
          responder={responder}
          setResponder={setResponder}
          selectedPractice={selectedPractice}
          setSelectedPractice={setSelectedPractice}
          handlePracticeStart={handlePracticeStart}
          rawCast={cifInterface.cif.rawCast}
          rawPractices={cifInterface.cif.rawPractices}
          handlePracticeSelected={handlePracticeSelected}
        />
      );
    }

    if (isInteractionStarted) {
      return (
        <PracticeView
          initiator={initiator}
          responder={responder}
          selectedPractice={selectedPractice}
          onActionSelected={handleActionSelected}
          actions={actions}
          isLoading={isLoading}
          cifInterface={cifInterface}
          performanceInfo={currentPerformanceInfo}
          action={currentPerformanceInfo?.chosenAction || actionLabel}
          volitionInfoAsStrings={cifInterface.mostRecentVolitionInfo?.volitionInfoAsStrings}
          isTerminal={isTerminal}
          onChooseNextInteraction={handleChooseNextInteraction}
        />
      );
    }

    return null;
  };

  return (
    <div className="game-container">
      {getCurrentView()}
    </div>
  );
}

export default Game; 