import React, { useState } from 'react';
import './Selector.css';

const CharacterSelector = ({ characters, onSelectButtonClicked, disabledCharacter }) => {
  const [selectedCharacter, setSelectedCharacter] = useState(null);

  const handleSelect = (index) => {
    setSelectedCharacter(index);
    onSelectButtonClicked(characters[index]);
  };

  return (
    <div className="selector-container">
      {characters.map((character, index) => (
        <button
          key={index}
          onClick={() => handleSelect(index)}
          disabled={character.name === disabledCharacter?.name}
          className={`selector-button ${selectedCharacter === index ? 'selected' : ''} ${character.name === disabledCharacter?.name ? 'disabled' : ''}`}
        >
          {character.name}
        </button>
      ))}
    </div>
  );
};

export default CharacterSelector;

