async function getGeneratedText(prompt) {
    if (whichGeneratorAPI == "gemini") {
        return getGeminiText(prompt);
    }
    if (whichGeneratorAPI == "ollama") {
        return getOllamaText(prompt);
    }
    throw new Error("Unrecognized generator API: " + whichGeneratorAPI);
}

// Access your API key (see "Set up your API key" above)
const genAI = new GoogleGenerativeAI(API_KEY);

async function getGeminiText(prompt) {
    const generationConfig = {
        maxOutputTokens: 100,
//        temperature: 0.9, // randomness
//        topP: 0.1,
//        topK: 16,
    };
    const safetySettings = [
        {
            category: HarmCategory.HARM_CATEGORY_HARASSMENT,
            threshold: HarmBlockThreshold.BLOCK_ONLY_HIGH,
        },
        {
            category: HarmCategory.HARM_CATEGORY_HATE_SPEECH,
            threshold: HarmBlockThreshold.BLOCK_ONLY_HIGH,
        },
        {
            category: HarmCategory.HARM_CATEGORY_SEXUALLY_EXPLICIT,
            threshold: HarmBlockThreshold.BLOCK_ONLY_HIGH,
        },
        {
            category: HarmCategory.HARM_CATEGORY_DANGEROUS_CONTENT,
            threshold: HarmBlockThreshold.BLOCK_ONLY_HIGH,
        },
    ];

    const model = genAI.getGenerativeModel(
      { model: "gemini-pro",
        generationConfig: generationConfig,
        safetySettings: safetySettings
      });

    let text = "Hi there.";
    let gotGoodText = false;
    let i = 0;
    while (i < 10 && !gotGoodText) {
        try {
            const result = await model.generateContent(prompt);
            const response = await result.response;
            text = response.text();
            gotGoodText = true;
        } catch(e) {
            console.log("***FORBIDDEN TEXT GENERATED***");
            console.log("Here is the error");
            console.log(e);
        }
        i++;
    }
    return text;
}

async function getOllamaText(prompt) {
    const query = {
        model: ollamaModel,
        prompt: prompt,
        stream: false
    };
    const result = await fetch("http://localhost:11434/api/generate",
                               { method: "POST",
                                 body: JSON.stringify(query) });
    const response = await result.json();
    return response.response;
}

