let generatePrompt = function(config) {
    let initiatorName = cifInterface.initiatorName;
    let responderName = cifInterface.responderName;

    //mostRecentVolitionInfo = {
    //    initiatorName: initiatorName,
    //    responderName: responderName,
    //    mts: scoredMTInfos,
    //    actions: actionInfos,
    //    selectedAction: selectedAction,
    //    volitionInfoAsStrings: volitionInfoAsStrings
    //}
    //mostRecentActionPerformedInfo = {
    //    initiator: initiatorName,
    //    responder: responderName,
    //    performance: dialogue,
    //    chosenAction: chosenAction
    //}
    //volitionInfoAsStrings = {
    //    "intentName": [
    //        ruleInfoInStringForm1,
    //        ruleInfoInStringForm2
    //     ]
    //}
    //ruleInfoInStringForm = {
    //    "description": rule.name,
    //    "predicates": [],
    //    "weight": rule.effects[0].weight,
    //    "intent": mtInfoName
    //};

    let subPrompts = [];

    let relevantPracticeSummaries = cifInterface.cif.practiceManager.getRelevantPracticeSummaries(initiatorName, responderName);
    let bindings = cifInterface.mostRecentVolitionInfo.selectedAction.chosenRoleBindings;
    let practice = cifInterface.cif.practiceManager.getCurrentPractice();
    let selectedAction = cifInterface.mostRecentVolitionInfo.selectedAction;

    if (config.personify.include) {
        subPrompts.push({
            order: config.personify.order,
            subPrompt: `You are a writer trying to come up with an appropriate line of dialogue for a character to say in a complex situation.\n`
        })
    }


    // shots: {
    //     include: true,
    //     telegraphExamples: true,
    //     count: 2,
    //     order: -999999
    // },
    if (config.shots.include) {
        let exampleCount = (config.shots.count < exampleShots.length) ? config.shots.count : exampleShots.length;

        if (config.shots.telegraphExamples) {
            subPrompts.push({
                order: config.shots.order - config.shots.count - 2,
                subPrompt: `Below are ${exampleCount} examples (between the <EXAMPLE> tags) that show INPUT and appropriate OUTPUT. Try to model your OUTPUT after the examples.\n`
            })
        }
        for (let i = 0; i < exampleCount; i++) {
            subPrompts.push({
                order: config.shots.order - config.shots.count + i - 1,
                subPrompt: getExampleShotSubPrompt(i, config.shots)
            })
        }
    }

    // General context
    if (config.generalContext.include) {
        let subPrompt = `${initiatorName} and ${responderName} are in a conversation inside a pizza parlor.\n`;
        subPrompt += "\n";
        subPrompt += `${initiatorName} and ${responderName} are engaged in a conversation that can generally be described as: ${practice.label}.\n`;
        subPrompts.push({
            order: config.generalContext.order,
            subPrompt: processSubPrompt(config.generalContext, subPrompt)
        });
    }

    // History of recent interactions (a summary string)
    if (config.interactionHistory.include) {
        if (relevantPracticeSummaries.length > 0) {
            let subPrompt = `Here is a list of ${initiatorName} and ${responderName}'s history of interactions in the recent past:\n`;
            for (let summary of relevantPracticeSummaries) {
                subPrompt += `- ${summary.summary}\n`
            }
            subPrompts.push({
                order: config.interactionHistory.order,
                subPrompt: processSubPrompt(config.interactionHistory, subPrompt)
            });
        }
    }

    // What has been said in the current practice so far
    if (config.previousDialogue.include) {
        if (cifInterface.practiceDialogueHistory.length > 0) {
            let subPrompt =  "So far, this is what they have said in this discussion: \n";
            let first = true;
            for (let history of cifInterface.practiceDialogueHistory) {
                let line = (first) ? "said the following" : "responded";
                first = false;
                subPrompt += "- " + history.speaker + " " + line + " with the intent of " + history.intent + ": \"" + history.line + "\". \n";
            }
            subPrompts.push({
                order: config.previousDialogue.order,
                subPrompt: processSubPrompt(config.previousDialogue, subPrompt)
            });
        }
    }   

    // Establish who is about to talk and with what intent
    if (config.speakerAndIntent.include) {
        let line = (cifInterface.practiceDialogueHistory.length > 0) ? "respond" : "say something";
        let subPrompt = initiatorName + " is going to " + line + " with the intent of being " + selectedAction.intent + ".\n"
        subPrompts.push({
            order: config.speakerAndIntent.order,
            subPrompt: processSubPrompt(config.speakerAndIntent, subPrompt)
        });
    }

    if (config.preconditions.include) {
        if (selectedAction.preconditions.length > 0) {
            let boundPreds = cifInterface.cif.practiceManager.getCloneOfBoundPredicates(selectedAction.preconditions, bindings);
            let firstPredPrinted = false;
            let subPrompt = "";
            for (let preconditionPred of boundPreds) {
                if (!firstPredPrinted) {
                    subPrompt += "This action was only available to be selected because the following precondition was met: \n"
                    firstPredPrinted = true;
                }        
                // subPrompt += `- ${cif.predicateToEnglish(preconditionPred).text}\n`;
                subPrompt += `- ${cifInterface.cif.predicateNLG(preconditionPred).text}\n`;
            }
            subPrompts.push({
                order: config.preconditions.order,
                subPrompt: processSubPrompt(config.preconditions, subPrompt)
            });
        }
    }

    let intentClass = cifInterface.cif.rawSchema.schema.find((element) => element.class === "intent");
    let mtInfos = [];
    for (let intent of intentClass.types) {
        mtInfos[intent] = getMTsFromMostRecentVolitionInfo(true, intent);
    }
    mtInfos.sort( (a,b) => a.score - b.score);

    // let mtToString = getMTsFromMostRecentVolitionInfo(true, mostRecentVolitionInfo.selectedAction.intent);
    // All of the positive reasons why the speaker has the intent
    if (config.mtWithPositiveWeight.include) {
        subPrompts.push({
            order: config.mtWithPositiveWeight.order,
            subPrompt: processSubPrompt(config.mtWithPositiveWeight, mtInfos[cifInterface.mostRecentVolitionInfo.selectedAction.intent].positiveCompleteString)
        });
    }
    // All of the reasons why the speaker doesn't have that intent
    if (config.mtWithNegativeWeight.include) {
        subPrompts.push({
            order: config.mtWithNegativeWeight.order,
            subPrompt: processSubPrompt(config.mtWithNegativeWeight, mtInfos[cifInterface.mostRecentVolitionInfo.selectedAction.intent].negativeCompleteString)
        });
    }
    // Positive other intent rules
    if (config.mtWithPositiveWeightOtherIntents.include) {
        let otherIntentsString = "";
        for (let intentKey in mtInfos) {
            if (intentKey !== cifInterface.mostRecentVolitionInfo.selectedAction.intent) {
                otherIntentsString += mtInfos[intentKey].positiveCompleteString;
            }
            otherIntentsString += "\n";
        }

        subPrompts.push({
            order: config.mtWithPositiveWeightOtherIntents.order,
            subPrompt: processSubPrompt(config.mtWithPositiveWeightOtherIntents, otherIntentsString)
        });
    }
    // Negative other intent rules
    if (config.mtWithNegativeWeightOtherIntents.include) {
        let otherIntentsString = "";
        for (let intentKey in mtInfos) {
            if (intentKey !== cifInterface.mostRecentVolitionInfo.selectedAction.intent) {
                otherIntentsString += mtInfos[intentKey].negativeCompleteString;
            }
            otherIntentsString += "\n";
        }

        subPrompts.push({
            order: config.mtWithNegativeWeightOtherIntents.order,
            subPrompt: processSubPrompt(config.mtWithNegativeWeightOtherIntents, otherIntentsString)
        });
    }

    // Establish the name of the action that is about to be taken (with intent)
    if (config.actionName.include) {
        let subPrompt = "The way " + initiatorName + " is going to act can be generally be described as '" + selectedAction.label + "' (with the intent of being " + selectedAction.intent + ").\n";
        subPrompts.push({
            order: config.actionName.order,
            subPrompt: processSubPrompt(config.actionName, subPrompt)
        });
    }

    // How the social state will change after the action is taken
    if (config.effects.include) {
        if (selectedAction.effects.length > 0) {
            let boundPreds = cifInterface.cif.practiceManager.getCloneOfBoundPredicates(selectedAction.effects, bindings);
            let firstEffectPrinted = false;
            let subPrompt = "";
            for (let effectPred of boundPreds) {
                if (!firstEffectPrinted) {
                    subPrompt += `After this action, the social state will be changed in the following way${(selectedAction.effects.length>1)?"s":""}: \n`
                    firstEffectPrinted = true;
                }        
                subPrompt += "- " + cifInterface.cif.predicateNLG(effectPred).text + "\n";
                // subPrompt += "- " + cif.predicateToEnglish(effectPred).text + "\n";
            }
            subPrompts.push({
                order: config.effects.order,
                subPrompt: processSubPrompt(config.effects, subPrompt)
            });
        }
    }

    // The actual command to generate a line
    if (config.requestToLLM.include) {
        let subPrompt = "Please create a single line of dialogue that " + initiatorName + " would say in the situation just described.\n";
        subPrompts.push({
            order: config.requestToLLM.order,
            subPrompt: processSubPrompt(config.requestToLLM, subPrompt)
        });
    }
        
    if (config.emphasisPriority.include) {
        // Now deal with emphasis
        let emphasizedSubPrompts = [];
        for (let configKey in config) {
            if (config[configKey].include && config[configKey].emphasize) {
                emphasizedSubPrompts.push(config[configKey]);
            }
        }
        emphasizedSubPrompts.sort((a, b) => (a.importance > b.importance ? -1 : b.importance > a.importance ? 1 : 0));

        let subPrompt = "";
        let count = 0;
        for (let emphasizedSubPrompt of emphasizedSubPrompts) {
            if (count === 0) {
                subPrompt += `The line of dialogue should try to emphasize the content contained within the following tags using the following order of priority:\n`;
            }
            count++;
            subPrompt += `${count}. <${emphasizedSubPrompt.tag}></${emphasizedSubPrompt.tag}>\n`
        }

        subPrompts.push({
            order: config.requestToLLM.order,
            subPrompt: processSubPrompt(config.requestToLLM, subPrompt)
        });
    }

    

    // Plead with llm to pay attention to the detail provided (emphasize what should be salient to the generator)
    if (config.plead.include) {
        let subPrompt = "The line should be two sentence at most, and should use simple language. Also, try to represent the reasons they intend to respond in that way in the line of dialogue as much as possible. Additionally, try to have them refer to their history of interactions from the past listed above.\n"
        subPrompts.push({
            order: config.plead.order,
            subPrompt: processSubPrompt(config.plead, subPrompt)
        });
    }

    // Plead with llm to pay attention to the detail provided (emphasize what should be salient to the generator)
    if (selectedAction.noteToLLM && selectedAction.noteToLLM !== "" && config.noteToLLM.include) {
        let subPrompt = `Here are some notes about this action to be considered while generating dialogue: ${selectedAction.noteToLLM}\n`
        subPrompts.push({
            order: config.noteToLLM.order,
            subPrompt: processSubPrompt(config.noteToLLM, subPrompt)
        });
    }

    // Player focused prototype for what behavior the agent is about to engage in (tone/intent)
    if (config.playerFocusedDialogue.include) {
        if (selectedAction.quote !== undefined) {
            let subPrompt = "The line should come across as communicating someting along the lines as '" + selectedAction.quote + "', but with more detail.\n";
            subPrompts.push({
                order: config.playerFocusedDialogue.order,
                subPrompt: processSubPrompt(config.playerFocusedDialogue, subPrompt)
            });
        }
    }
    
    // Character speech style
    if (config.speechStyle.include) {
        let subPrompt = initiatorName + "'s style of speech should be able to be described as " + cifInterface.cif.getCharData(initiatorName).speechStyle + ".\n";
        subPrompts.push({
            order: config.speechStyle.order,
            subPrompt: processSubPrompt(config.speechStyle, subPrompt)
        });
    }

    // Character pronouns
    if (config.pronouns.include) {
        let subPrompt = `${initiatorName} uses the pronouns ${cifInterface.cif.getCharData(initiatorName).pronouns}, and ${responderName} uses the pronouns ${cifInterface.cif.getCharData(responderName).pronouns}.\n`;
        subPrompts.push({
            order: config.pronouns.order,
            subPrompt: processSubPrompt(config.pronouns, subPrompt)
        });
    }    
    
    // Character Notes
    if (config.characterNotes.include) {

        let initiatorCharacterNotes = cifInterface.cif.getCharData(initiatorName).characterNotes;
        let responderCharacterNotes = cifInterface.cif.getCharData(responderName).characterNotes;

        let subPrompt = `Here are some facts about ${initiatorName} and ${responderName}:\n`;
        for (let charName in initiatorCharacterNotes) {
            if (charName === responderName) {
                for (let charNote of initiatorCharacterNotes[charName]) {
                    subPrompt += `- ${charNote}\n`
                }
            }
        }
        for (let charName in responderCharacterNotes) {
            if (charName === initiatorName) {
                for (let charNote of responderCharacterNotes[charName]) {
                    subPrompt += `- ${charNote}\n`
                }
            }
        }

        subPrompts.push({
            order: config.characterNotes.order,
            subPrompt: processSubPrompt(config.characterNotes, subPrompt)
        });
    }

    // Style request 1: informal and not stilted
    if (config.avoidStiltedness.include) {
        let subPrompt = "Before you generate the line, most of the time the dialogue that you generate sounds overly formal. Please try to make the line sound conversational and informal if anything.\n"
        subPrompts.push({
            order: config.avoidStiltedness.order,
            subPrompt: processSubPrompt(config.avoidStiltedness, subPrompt)
        });
    }

    // Style request 2: genre
    if (config.genre.include) {
        let subPrompt = "The generated line of dialogue should be in the style of a television sitcom line of dialogue.\n"
        subPrompts.push({
            order: config.genre.order,
            subPrompt: processSubPrompt(config.genre, subPrompt)
        });
    }


    if (config.knowledgeOfNames.include) {
        let conditions = [
            {
                category: "relationship",
                type: "met",
                first: "x",
                second: "y",
                value : true
            }
        ]
        let result = cifInterface.cif.practiceManager.evaluateRule(conditions, bindings, cifInterface.cif.getCharacters());
        let knowNames = (!result.success) ? "do not know" : "know";
        let subPrompt = `${initiatorName} and ${responderName} ${knowNames} each other's names.\n`;
        subPrompts.push({
            order: config.knowledgeOfNames.order,
            subPrompt: processSubPrompt(config.knowledgeOfNames, subPrompt)
        });
    }


    // if (config.emphasisSymbol.include) {
    //     let subPrompt = `Whevever the symbol '${emphasisSymbolStart}' appears at the start of a line in the prompt, make sure that the information is incorporated into the line of dialouge. The dialogue shoud never contain any of the following symbols: 'RESPONSE', '<', '>', or '/'.\n`;
    //     subPrompts.push({
    //         order: config.emphasisSymbol.order,
    //         subPrompt: processSubPrompt(config.emphasisSymbol, subPrompt)
    //     });
    // }

    subPrompts.sort((a, b) => (a.order > b.order ? 1 : b.order > a.order ? -1 : 0));

    let prompt = "";
    for (let subPrompt of subPrompts) {
        if (prompt !== "") {
            prompt += "\n";
        }
        prompt += subPrompt.subPrompt;
    }

    prompt = "INPUT:\n" + prompt + "\n\nOUTPUT:\n";

    return prompt;
}

// This will add emphasis and importance to an entire subPrompt section (i.e. it is multi-line).
let processSubPrompt = function(config, subPrompt) {
    let subPromptLines = subPrompt.split(/\r?\n/);
    let processedSubPrompt = "";
    if (config.useTag) {
        processedSubPrompt += `<${config.tag}>\n`;
    }
    let first = true;
    for (let subPromptLine of subPromptLines) {
        if (subPromptLine !== '') {
            if (config.include && config.emphasize) {
                if (subPromptLines.length > 1 && first) {
                    // for the multi line ones, don't add emphasis on the first line?
                    first = false;
                    subPromptLine = `${subPromptLine}`;
                } else {
                    subPromptLine = `${emphasisSymbolStart}${subPromptLine}${emphasisSymbolEnd}`;
                    // subPromptLine += (config.importance !== -1) ? importanceSymbol(config.importance): "";
                }
            }
            subPromptLine += "\n";
            processedSubPrompt += subPromptLine;
        }
    }
    if (config.useTag) {
        processedSubPrompt += `</${config.tag}>\n`;
    }
    return processedSubPrompt;
}



let getMTsFromMostRecentVolitionInfo = function(addContextText, intent) {

    let mtToString = {
        positiveCompleteString: "",
        negativeCompleteString: "",
        score: 0
    }

    let positiveRules = [];
    let negativeRules = [];
    // console.log(mostRecentVolitionInfo.mts);
    // console.log("8888888888");
    // console.log(intent);
    for (let rule of cifInterface.mostRecentVolitionInfo.mts[intent].trueRules) {
        mtToString.score += rule.score;
        if (rule.score > 0) {
            positiveRules.push(rule);
        } else if (rule.score < 0) {
            negativeRules.push(rule);
        }
    }
    if (positiveRules.length > 0) {
        mtToString.positiveCompleteString = (addContextText)?`The reasons ${cifInterface.mostRecentVolitionInfo.initiatorName} has the intent of being ${intent} to ${cifInterface.mostRecentVolitionInfo.responderName} are:\n`:"";
        for (let rule of positiveRules) {
            // Here are the two options:
            // 1. mostRecentVolitionInfo.selectedAction.chosenRoleBindings // doesn't appear to have 'other' roles bound
            // 2. rule.result.possibleRoleBindings[0] // This works!
            let ruleString = cifInterface.cif.practiceManager.volitionRuleToString(rule, rule.result.possibleRoleBindings[0], true);
            mtToString.positiveCompleteString += `- ${rule.title} (+${rule.effects[0].weight}): ${ruleString}\n`;
        }
    }
    if (negativeRules.length > 0) {
        mtToString.negativeCompleteString = (addContextText)?`The reservations ${cifInterface.mostRecentVolitionInfo.initiatorName} has about having the intent of being ${intent} to ${cifInterface.mostRecentVolitionInfo.responderName} are:\n`:"";
        for (let rule of negativeRules) {
            let ruleString = cifInterface.cif.practiceManager.volitionRuleToString(rule, rule.result.possibleRoleBindings[0], true);
            mtToString.negativeCompleteString += `- ${rule.title} (${rule.effects[0].weight}): ${ruleString}\n`;
        }
    }
    return mtToString;
}


let ruleToStringWithPredNotation = function(rule, bindings) {
    let ruleString = "";
    let boundConditions = cif.practiceManager.getCloneOfBoundPredicates(rule.conditions, bindings);
    for (let i = 0; i < boundConditions.length; i++) {
        
        ruleString += `${predicateObjectToPredicateString(boundConditions[i], false)}`;
        ruleString += `${(i<boundConditions.length-1)?"^ ":""}`;
    }
    
    ruleString += ` -> ${predicateObjectToPredicateString(rule.effects[0], true)}`
    return ruleString;
}

function predicateObjectToPredicateString(predObj, isEffect) {
    let schemaClass = cifInterface.cif.rawSchema.schema.find((element) => element.class === predObj.class);

    let predString = "";

    if (schemaClass.isBoolean && !predObj.value) {
        predString += "~";
    }

    predString += predObj.type;
    predString += "(";
    predString += predObj.first;
    if (schemaClass.directionType === "directed" || schemaClass.directionType === "reciprocal") {
        predString += ",";
        predString += predObj.second;
    }
    if (predObj.operator !== undefined && predObj.operator !== "" && !schemaClass.isBoolean) {
        predString += ",";
        predString += predObj.operator;
        
        if (schemaClass.class === "intent") {
            predString += ",";
            predString += (predObj.weight >= 0) ? "+,": "-,";
            predString += Math.abs(predObj.weight);
        } else if (schemaClass.class === "network") {
            predString += ",";
            predString += (!isEffect) ? predObj.value : predObj.value;
        } else if (schemaClass.class === "history") {
            predString += ",";
            predString += getHistoryWindow(predObj.turnsAgoBetween);
        } else if (predObj.value !== undefined && predObj.value !== "") {
            predString += ",";
            predString += predObj.value;
        } 
    }
    predString += ")"

    return predString;
}

function getHistoryWindow(turnsAgoBetween) {
    if (turnsAgoBetween[0] === 0 && turnsAgoBetween[1] === 8) {
        return "recently";
    } else if (turnsAgoBetween[0] === 0 && turnsAgoBetween[1] === 2) {
        return "just_now";
    }
}


let importanceSymbol = function(importance) {
    let symbol = "<-IMPORTANT";
    for (let i = 0; i < importance; i++) {
        symbol += "!";
    }
    return symbol;
}


let getExampleShotSubPrompt = function (index, config) {
    let subPrompt = "";
    subPrompt += `<EXAMPLE>\n`;
    subPrompt += `INPUT:\n`
    subPrompt += exampleShots[index].prompt;
    subPrompt += "\n";
    // subPrompt += `RESPONSE ${index + 1}:\n`
    subPrompt += `OUTPUT:\n`
    subPrompt += exampleShots[index].response;
    subPrompt += `</EXAMPLE>\n`;
    subPrompt += `\n`;
    subPrompt += (config.rationale) ? `Here's the reasons why that would be considered a good response: ${exampleShots[index].rationale}\n`:``;
    return subPrompt;
}


let generatePracticeSummary = async function() {
    // Time to generate a PRS
    let prompt = cifInterface.initiatorName + " and " + cifInterface.responderName + " just engaged in a conversation generally referred to as: " + cifInterface.cif.practiceManager.getCurrentPractice().label + ". Below is a list of what was said:\n";
    let first = true;
    for (let history of cifInterface.practiceDialogueHistory) {
        let line = (first) ? "said the following" : "responded";
        first = false;
        // prompt += "- " + history.speaker + " " + line + " with the intent of " + history.intent + ": \"" + history.line + "\". \n";
        prompt += "- " + history.speaker + " " + line + ": \"" + history.line + "\". \n";
    }
    prompt += "Please create a short sentence that summarizes what was said.";
    prompt += ` ${cifInterface.initiatorName} uses the pronouns ${cifInterface.cif.getCharData(cifInterface.initiatorName).pronouns}, and ${cifInterface.responderName} uses the pronouns ${cifInterface.cif.getCharData(cifInterface.responderName).pronouns}.`
    console.log(prompt);
    console.log("***************");
    let prs = await getGeminiText(prompt);
    let prsObj = {
        initiator: cifInterface.initiatorName,
        responder: cifInterface.responderName,
        summary: prs,
        // dialogue: cif.util.clone(practiceDialogueHistory), //history.speaker, history.line
        dialogue: structuredClone(cifInterface.practiceDialogueHistory),
        practice: cifInterface.cif.practiceManager.getCurrentPractice().label,
        time: cifInterface.cif.sfdb.getCurrentTimeStep() - cifInterface.practiceDialogueHistory.length
    }
    return prsObj;
}


let generateAllAtOnce = async function(practiceName, initiatorName, responderName, actionPerformedInfos) {
    let prompt = `${initiatorName} and ${responderName} had a conversation generally described as ${practiceName}.\n`;
    prompt += `Here's an abstract summary of what they said and why they said it:\n`;
    for (let api of actionPerformedInfos) {
        prompt += `- ${api.initiator} said something that can be described as ${api.chosenAction.label} with the intent of being ${api.chosenAction.intent}.\n`
    }
    prompt += `Can you please generate a conversation between ${initiatorName} and ${responderName} where each item in the list represented with a line of dialogue?\n`;
    let conversation = await getGeminiText(prompt);
    console.log("--------");
    console.log(prompt);
    console.log("--------");
    console.log(conversation);
}



