import React, { useState } from 'react';
import './Selector.css';

const PracticeSelector = ({ practices, onSelectPractice }) => {
  const [selectedPractice, setSelectedPractice] = useState(null);

  const handleSelect = (index, practice) => {
    setSelectedPractice(index);
    onSelectPractice(practice.label);
  };

  return (
    <div className="selector-container">
      {practices.map((practice, index) => (
        <button
          key={index}
          onClick={() => handleSelect(index, practice)}
          className={`selector-button ${selectedPractice === index ? 'selected' : ''}`}
        >
          {practice.label}
        </button>
      ))}
    </div>
  );
};

export default PracticeSelector; 