import cif from './CiF.js';

import * as nlg from '../../cif-spe-bridge/llm-nlg.js';
// import * as experiments from '../../cif-spe-bridge/experiments.js';


const useGeneratedDialgoue = true;
const useGeneratedPracticeSummaries = true;
let practiceDialogueHistory = [];
let mostRecentVolitionInfo = {};

let simulationName = "Ensemble with Social Practices";

let initiatorName = "";
let responderName = "";

let launchPractice = function(practiceName, initiator, responder) {
    // Set these are GLOBAL and NECESSARY
    initiatorName = initiator;
    responderName = responder;

    // reset the dialogue history that we use for prompt generation
    practiceDialogueHistory = [];

    cif.practiceManager.setCurrentPractice(practiceName);

    console.log("Launching practice '" + practiceName + "' between " + initiatorName + " and " + responderName);

    let currentPractice = cif.practiceManager.getCurrentPractice();
    cif.practiceManager.launchStage(currentPractice.entryStage, initiatorName, responderName);
    cif.practiceManager.generateActions(false);
}


// After this function is called, 'mostRecentVolitionInfo' contains
// all of the information about the the volitions and who is talking, etc.
let getActions = function() {
    cif.practiceManager.generateActions(false);

    let selectedAction = cif.practiceManager.selectAction(cif.getCharacters(), cif.practiceManager.getPossibleActions());

    let scoredMTInfos = cif.practiceManager.getScoredMTInfo();
    let actionInfos = cif.practiceManager.getScoredActionsInfo();

    let volitionInfoAsStrings = cif.practiceManager.getVolitionInfoAsStrings();
    
    mostRecentVolitionInfo = {
        initiatorName: initiatorName,
        responderName: responderName,
        mts: scoredMTInfos,
        actions: actionInfos,
        selectedAction: selectedAction,
        volitionInfoAsStrings: volitionInfoAsStrings
    }
}

// This will make an action happen, apply its effects, and send the dialogue.
// This assumes that we are having initiatorName perform the action to responderName
let performAction = async function(action) {

    console.log("Launching action '" + action + "' from " + initiatorName + " to " + responderName);

    // Get the scored action from the mostRecentVolitionInfo and update
    let chosenAction = cif.practiceManager.getActionByName(action);

    // NOTE: Action info's aren't actions... so go get the info from the mostRecentVolitionInfo
    let actionInfo = mostRecentVolitionInfo.actions.find(a => a.label === action);
    chosenAction.score = actionInfo.score;

    // ?????? ******** -----------
    // Sometimes we choose an action that isn't what the system chose, so...
    // overwrite mostRecentVolitionInfo's value that was set in get-actions.
    // I guess? Definitely keep an eye on this.
    mostRecentVolitionInfo.selectedAction = chosenAction;
    // ?????? ******** -----------

    cif.practiceManager.launchStage(cif.practiceManager.getCurrentStage(), initiatorName, responderName);

    cif.practiceManager.setBestRoleBindingForAction(chosenAction, cif.getCharacters());

    cif.practiceManager.applyEffects();
    
    let effectsStateEffectText = [];
    for (let effect of chosenAction.effects) {
        let str = effect.type;
        if (effect.value !== undefined) {
            str += " ";
            if (effect.value < 0 || (effect.operator !== undefined && effect.operator === "-")) {
                str += "-";
            } else {
                str += "+";
            }
            str += effect.value;
        }
        
        effectsStateEffectText.push(str);
    }
    let effectPredicateTexts = [];
    let bindings = mostRecentVolitionInfo.selectedAction.chosenRoleBindings;
    if (mostRecentVolitionInfo.selectedAction.effects.length > 0) {
        let boundPreds = cif.practiceManager.getCloneOfBoundPredicates(mostRecentVolitionInfo.selectedAction.effects, bindings);
        for (let effectPred of boundPreds) {
            effectPredicateTexts.push(cif.predicateNLG(effectPred));
            // effectPredicateTexts.push(cif.predicateToEnglish(effectPred).text);
        }
    }

    let dialogue = "Hello there."; // Default dialogue.
    let prompt = "default";
    if (useGeneratedDialgoue) {
        prompt = nlg.generatePrompt(nlg.promptConfig);
        // console.log("*** PROMPT ***\n" + prompt + "\n-------------\n");
        dialogue = await nlg.getGeneratedText(prompt);
        dialogue = dialogue.replace(/"/g, "");
    } else {
        dialogue = cif.practiceManager.instantiatePerformance();
    }

    // Send back info about what just happened
    let actionPerformedInfo = {
        initiator: initiatorName,
        responder: responderName,
        performance: dialogue,
        chosenAction: chosenAction,
        prompt: prompt,
        effectsStateEffectText: effectsStateEffectText,
        effectPredicateTexts: effectPredicateTexts
    }


    practiceDialogueHistory.push({
        speaker: initiatorName,
        line: dialogue,
        intent: chosenAction.intent,
        actionName: chosenAction.label,
        prompt: prompt,
        mts: mostRecentVolitionInfo.mts[chosenAction.intent].trueRules
    });


    // Move into next stage
    cif.practiceManager.transitionStageToTakenActionsStage();

    // Change roles to get ready for the next get-actions
    let tmp = initiatorName;
    initiatorName = responderName;
    responderName = tmp;
    cif.practiceManager.launchStage(cif.practiceManager.getCurrentStage(), initiatorName, responderName);
    
    return actionPerformedInfo;
}

// This will return an object with "isTerminal" which will tell us whether or not we should keep going
// Kind of weird place to do this, but if we are terminal we generate a summary
let isTerminal = async function() {
    let terminal = cif.practiceManager.inTerminalStage();

    if (terminal) {
        if (useGeneratedPracticeSummaries) {
            cif.practiceManager.addPracticeSummary(await nlg.generatePracticeSummary());
        } else {
            let prsObj = {
                initiator: initiatorName,
                responder: responderName,
                summary: `${initiatorName} and ${responderName} just engaged in a conversation generally referred to as: ${cif.practiceManager.getCurrentPractice().label}.`,
                dialogue: structuredClone(practiceDialogueHistory),
                practice: cif.practiceManager.getCurrentPractice().label,
                time: cif.sfdb.getCurrentTimeStep() - cifInterface.practiceDialogueHistory.length
            }            
            cif.practiceManager.addPracticeSummary(prsObj);
        }
    }

    return terminal;
}


let initializeCiF = () => {    
    cif.init();

    cif.loadSocialStructure(cif.rawSchema);
    let cast = cif.addCharacters(cif.rawCast);
    cif.practiceManager.setCast(cast);
    cif.practiceManager.resetPracticeSummaries();
    for (let practice of cif.rawPractices.socialPractices) {
        cif.practiceManager.addPractice(practice);
    }
    for (let rawRuleSet of cif.rawMTSet.microtheories) {
        cif.addRules(rawRuleSet);
    }
    cif.addHistory(structuredClone(cif.rawHistory));

    console.log(simulationName + " has been initialized");

    // This should be moved into experiments I think... But would experiments need an initialize?
    for (let subPromptType in nlg.promptConfig) {
        nlg.promptConfig[subPromptType].include = nlg.config_modes[nlg.promptStyle][subPromptType];
        nlg.promptConfig[subPromptType].useTag = nlg.promptConfig[subPromptType].useTag && (nlg.config_modes[nlg.promptStyle]["emphasisPriority"] || nlg.config_modes[nlg.promptStyle]["shots"]);
    }
}


// Export everything at the end of the file
export {
    mostRecentVolitionInfo,
    practiceDialogueHistory,
    initiatorName,
    responderName,    
    launchPractice,
    getActions,
    performAction,
    isTerminal,
    initializeCiF,
    cif
};